import React, { Component } from 'react'
import SideMenu from './SideMenu'
import Main from './Main'
import Output from './Output'

export class Body extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            selecetedSchema: -1,
            allSchemas: [],
            isMobileDevice: window.innerWidth < 400,
        }
    }
    
    getStyle = () => {
        return {
            flexGrow: 1,
            height: '100%',
            display: 'flex',
            flexDirection: 'row',
        }
    }

    createNewSchema = (schemaname) => {
        const allSchemas = this.state.allSchemas;
        allSchemas.push({name: schemaname, properties: [], subSchemas: [], subSchemaNames: []});
        this.setState({
            allSchemas: allSchemas,
            selecetedSchema: allSchemas.length-1
        });
    }


    addSubSchema = (id, isArray) => {
        const subSchema = this.state.allSchemas[id];
        const allSchemas = this.state.allSchemas;
        const schema = allSchemas[this.state.selecetedSchema];
        schema.subSchemaNames.push(subSchema.name);
        schema.subSchemas.push({
            name: subSchema.name,
            properties: subSchema.properties,
            subSchemas: subSchema.subSchemas,
            subSchemaNames: schema.subSchemaNames.concat(subSchema.subSchemaNames),
            isArray: isArray
        });

        allSchemas[this.state.selecetedSchema] = schema;
        this.setState({
            allSchemas
        });
        setTimeout(()=>{console.log(this.state.allSchemas)},2000);
        

    }

    selectSchema = (id) => {
        // console.log("Selected Schema: ", this.state.allSchemas[id]);
        this.setState({
            selecetedSchema: id
        })
    }

    deleteSchema = () => {
        const allSchemas = this.state.allSchemas;
        allSchemas.splice(this.state.selecetedSchema, 1);
        this.setState({
            selecetedSchema: this.state.selecetedSchema-1,
            allSchemas
        })
    }



    addNewProperty = (property) => {
        const allSchemas = this.state.allSchemas;
        allSchemas[this.state.selecetedSchema].properties.push(property);
        this.setState({
            allSchemas
        })
    }

    deleteProperty = (index) => {
        const allSchemas = this.state.allSchemas;
        allSchemas[this.state.selecetedSchema].properties.splice(index, 1);
        this.setState({
            allSchemas
        })
    }

    deleteSubSchema = (index) => {
        const allSchemas = this.state.allSchemas;
        allSchemas[this.state.selecetedSchema].subSchemas.splice(index, 1);
        this.setState({
            allSchemas
        })
    }

    render() {
        return (
            <div style={this.getStyle()}>
                <SideMenu isMobileDevice={this.state.isMobileDevice} allSchemas={this.state.allSchemas} selectedSchema={this.state.selecetedSchema} selectSchema={this.selectSchema} createNewSchema={this.createNewSchema}/>
                <Main isMobileDevice={this.state.isMobileDevice} schema={this.state.allSchemas[this.state.selecetedSchema]} deleteSchema={this.deleteSchema} addNewProperty={this.addNewProperty} deleteProperty={this.deleteProperty} deleteSubSchema={this.deleteSubSchema} allSchemas={this.state.allSchemas} addSubSchema={this.addSubSchema} />
                <Output isMobileDevice={this.state.isMobileDevice} schema={this.state.allSchemas[this.state.selecetedSchema]} />
            </div>
        )
    }
}

export default Body
