import React, { Component } from 'react';
import RadioBox from './RadioBox';
export class GetSubSchema extends Component {
    
    constructor(props) {
        super(props)
        this.booleans = ['False', 'True']
        this.state = {
            showList: false,
            subSchema: null,
            subSchemaId: null,
            array: 1,
            error: false
        }
    }

    getStyle = () => {
        return {
            display: 'flex',
            alignItems: 'center'
        }
    }

    getSelectInputStyle = () => {
        return {
            color: 'white',
            backgroundColor: 'rgb(21,21,21)',
            display: 'flex',
            flexDirection: 'column',
            width: '150px',
        }
    }

    getSelectStyle = () => {
        return {
            display: 'flex',
            flexDirection: 'row',
        }
    }
    
    getSelectedStyle = () => {
        return {
            color: 'white',
            backgroundColor: '#26ae60',
            fontWeight: 'bold',
            padding: '2px 5px',
            flexGrow: 1,
        }
    }

    getErrorStyle = () => {
        return{
            color: 'crimson'
        }
    }

    getOptionListStyle = () =>  {
        return {
            display: this.state.showList ? 'flex': 'none',
            flexDirection: 'column',
            position: 'absolute',
            overflow: 'auto',
            zIndex: 10,
        }
    }
    
    getOptionStyle = () => {
        return {
            padding: '2px 5px', 
            backgroundColor: 'rgb(21,21,21)',
            width: '150px',
            borderBottom: '2px solid #333333', 
            cursor: 'pointer'
        }
    }

    toggleShowList = () => {
        const showList = this.state.showList;
        this.setState({
            showList: !showList,
        })
    }

    selectSchema = (name, id) => {
        this.setState({
            subSchema: name,
            subSchemaId: id
        })
    }
    
    setFormState = (key, value) => {
        this.setState({
            [key]: value
        })
    }

    addSubSchema = () => {
        if(!this.state.subSchema){
            this.setState({
                error: true
            });
            return;
        }

        this.props.addSubSchema(this.state.subSchemaId, this.state.array);


        this.setState({
            error: false,
            showList: false,
            subSchema: null
        })
    }

    getOptionsList = () => {
        const allSchemas = this.props.allSchemas;
        const selectedSchema = this.props.schema;
        return allSchemas.map((schema, i)=>{
            if(schema.subSchemaNames.includes(selectedSchema.name) || selectedSchema.name===schema.name || selectedSchema.subSchemaNames.includes(schema.name)) return '';
            return <div style={this.getOptionStyle()} key={i} onClick={()=>{this.selectSchema(schema.name, i)}}>{schema.name}</div>
        });
    }

    render() {
        return (
            <div style={this.getStyle()}>
                <div>
                    <div style={{fontSize: 'smaller',fontWeight: 'bold',padding: '2px'}}>Add sub schema</div>
                    <div style={this.getSelectInputStyle()}>
                    <div style={this.getSelectStyle()}>
                        <span style={this.getSelectedStyle()}>{this.state.subSchema ? this.state.subSchema : 'All Schemas'}</span>
                        <span style={{cursor: 'pointer'}} onClick={this.toggleShowList}>
                            <i className="material-icons">{this.state.showList ? 'keyboard_arrow_up' : 'keyboard_arrow_down'}</i>
                            </span>
                        </div>
                        <div style={{position: 'relative'}}>
                            <div style={this.getOptionListStyle()}>
                                {this.getOptionsList()}
                            </div>
                        </div>
                    </div>
                </div>
                <div>
                    <RadioBox selected={this.state.array} title={"Array"} options={this.booleans} setFormState={this.setFormState} stateVar={'array'} />
                </div>
                <div>
                    <div style={{fontSize: 'smaller',fontWeight: 'bold',padding: '2px'}}>&nbsp;</div>
                    <i style={{fontSize: '35px', cursor: 'pointer'}} onClick={this.addSubSchema} className="material-icons">add_circle</i>
                </div>
                {
                this.state.error ? 
                    <div style={this.getErrorStyle()}>
                        <div style={{fontSize: 'smaller',fontWeight: 'bold',padding: '2px'}}>&nbsp;</div>
                        <div>Please select a schema to add.</div>
                    </div>
                    :
                    ''
                }
            </div>
        )
    }
}

export default GetSubSchema
