import React, { Component } from 'react';
import Title from './Title';

export class SchemaCode extends Component {

    getPropertyCode = (p, pntab) =>{

        let ptab = pntab + '\t';

        const opening = `${pntab}${p.name}: {\n`
        const type = `${ptab}type: ${p.type},\n`;
        const required = `${ptab}required: ${p.isRequired},\n`;
        const select = `${ptab}select: ${p.isSelected},\n`
        const defaultValue = p.defaultValue ? `${ptab}default: ${p.defaultValue},\n` : ``;
        const closing = `${pntab}},\n`
        const code = opening+type+required+select+defaultValue+closing;

        return code;
    }

    getAllPropertiesCodes = (properties, ptab) => {
        ptab+='\t';
        const allProperties = properties.map((property)=>{
            return this.getPropertyCode(property, ptab);
        })
        let code='';
        allProperties.forEach(property=>{
            code += property
        })
        return `${code}`
    }

    getAllSubSchemaCodes = (subSchemas, tab) => {
        tab+='\t';
        let code='';

        subSchemas.forEach((subSchema, i)=>{
            code+=`${tab}${subSchema.name}: ${subSchema.isArray ? '[{\n' : '{\n'}`;
            code+=this.getAllPropertiesCodes(subSchema.properties, tab);
            code+=this.getAllSubSchemaCodes(subSchema.subSchemas, tab);
            code+=`${subSchema.isArray ? `${tab}}],\n` : `${tab}},\n`}`;
        });

        return `${code}`;
    }

    getSchemaCode = ()=>{
        return (
`const mongoose = require('mongoose');
const Schema = mongoose.Schema;\n
const ${this.props.schema.name}Schema = new Schema({
${this.getAllPropertiesCodes(this.props.schema.properties, '\t')}
${this.getAllSubSchemaCodes(this.props.schema.subSchemas, '\t')}
});\n

module.exports = mongoose.model('${this.props.capitalize(this.props.schema.name)}', ${this.props.schema.name}Schema);\n
`
        )
    }

    render() {
        return (
            <div onClick={()=>{this.props.changeInView(0)}} >
                <Title text={'Schema Code'} getCopyButtonStyle={this.props.getCopyButtonStyle} toCopy={['schema']} copyCallback={this.props.copyContent} />
                <div style={this.props.getContentStyle(0)} >
                    <textarea wrap="off" readOnly='readonly' value={this.getSchemaCode()} id='schema'>
                    </textarea>
                    
                </div>
            </div>
        )
    }
}

export default SchemaCode
