import React, { Component } from 'react'
import Property from './Property'
import NewPropertyForm from './NewPropertyForm'
import SubSchema from './SubSchema';

export class Properties extends Component {

    getStyle = () => {
        return {
            // border: '5px solid cadetblue',
            flexGrow: 1,
            display: 'flex',
            flexDirection: 'column',
        }
    }
    getScrollableStyle = () => {
        return {
            // border: '5px solid purple',
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            overflow: 'auto',

        }
    }

    render() {
        return (
            <div style={this.getStyle()}>
                <NewPropertyForm addNewProperty={this.props.addNewProperty} allSchemas={this.props.allSchemas} allPropertyNames={this.props.schema.properties.map((p)=>{return p.name})} addSubSchema={this.props.addSubSchema} schema={this.props.schema}/>
                <div style={{position: 'relative', flexGrow: 1}}>
                <div style={this.getScrollableStyle()}>
                    
                    {this.props.schema.properties.map((property, i)=>{
                        return <Property property={property} key={i} index={i} deleteProperty={this.props.deleteProperty} />
                    })}
                    {this.props.schema.subSchemas.map((subSchema, i)=>{
                        return <SubSchema subSchema={subSchema} key={i} index={i} deleteSubSchema={this.props.deleteSubSchema} />
                    })}
                </div>
                </div>
            </div>
        )
    }
}

export default Properties
